
import { environment } from "src/environments/environment";

declare let CryptoJS: any;

export const decryption = (data:any)=>{
    var iv = CryptoJS.enc.Utf8.parse(environment.encryptionIV);
    var key = CryptoJS.enc.Utf8.parse(environment.encryptionKey);
    var decrypted =  CryptoJS.AES.decrypt(data, key, { iv: iv, mode: CryptoJS.mode.CBC});
    return decrypted.toString(CryptoJS.enc.Utf8);
}


export const  encryption = (data:any)=>{
    var iv = CryptoJS.enc.Utf8.parse(environment.encryptionIV);
    var key = CryptoJS.enc.Utf8.parse(environment.encryptionKey);
    var decrypted =  CryptoJS.AES.encrypt(data, key, { iv: iv, mode: CryptoJS.mode.CBC});
    return decrypted.toString();
}
